<template>
	<div>
		<el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="TRENDS" name="/Detail/trends">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="ENTRIES" name="/Detail/entries">
      <span slot="label">
        ENTRIES
        <el-badge v-if="entry_count != 0" :value="entry_count"csize="mini" class="item"></el-badge>
      </span>
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="SUGGESTIONS" name="/Detail/suggestions">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="ALERTS" name="/Detail/alerts">
      <router-view></router-view>
    </el-tab-pane>
    <el-tab-pane label="PROFILE" name="/Detail/profile">
      <router-view></router-view>
    </el-tab-pane>
  </el-tabs>
	</div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        activeName: 'second',
        newpath: 'default',
        name: '',
        detail: '',
        entry_count: 0,
        new_entry_count: 0,
      };
    },
    computed: {
      patient_id() {
        return this.$store.state.patient_id
      }
    },
    methods: {
      handleClick(tab, event) {
        name = this.$route.path.split('/')[1]
        this.newpath = '/' + name + this.activeName
        console.log(this.activeName)
        if (this.activeName == '/Detail/entries') {
          this.entry_count = 0
        }
        this.$router.push({path: this.newpath})
      }
    },
    created() {
      if(this.$route.path.split('/').length < 3) {
        this.newpath = this.$route.path + '/trends'
        this.$router.push({path: this.newpath})
      }
      this.activeName = '/Detail/' + this.$route.path.split('/')[3]
    },
    mounted() {
      var index = 0
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         response.data.forEach(el => {
          if (el.share == 1) {
            index ++;
          }
         })
         this.entry_count = index
         this.$forceUpdate()
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    watch: {
      patient_id(oldval, newval) {
        var index = 0
      var get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         response.data.forEach(el => {
          index ++
         })
         this.entry_count = index
         this.$forceUpdate()
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })

    },
      '$route' (to, from) {
      if(this.$route.path.split('/').length < 3) {
        this.newpath = this.$route.path + '/trends'
        this.$router.push({path: this.newpath})
      }
      this.activeName = '/Detail/' + this.$route.path.split('/')[3]
      console.log(this.activeName)
      }
    },
  };
</script>

<style>
  .el-tabs__item {
    width: 140px !important;
    padding: 0px !important;
    text-align: center;
    letter-spacing: 1px;
    font-weight: bold;
  }
  .el-tabs__active-bar {
    width: 140px !important;
  }
